<template>
  <svg 
    ref="svg"
    width="100%" 
    height="100%" 
    viewBox="0 0 150 150" 
    preserveAspectRatio="none" 
    xmlns="http://www.w3.org/2000/svg"
  >
    <rect width="150" height="150" :fill="pri"/>
    <circle ref="circle" cx="75" r="75" :fill="sec"/>
  </svg>
</template>

<script>
import anime from 'animejs';

export default {
  name: 'Halfmoon',
  data: function () {
    return {
      animIntro: null,
      animMain: null
    }
  },
  computed: {},
  props: {
    pri: String,
    sec: String
  },
  components: {},
  methods: {
    playMain: function () {
      this.animMain.play();
    },
    playIntro: function () {
      this.animIntro.play();
    },
    initIntro: function () {
      this.animIntro = anime({
        duration: 500,
        targets: this.$refs.circle,
        easing: 'easeOutQuad',
        loop: false,
        autoplay: false,
        cy: [-75, 0]
      }) 
    },
    initMain: function () {
      this.animMain = anime.timeline({
        targets: this.$refs.circle,
        loop: false,
        autoplay: false
      })
      .add({
        duration: 500,
        easing: 'easeInCirc',
        cy: [0, 300],
      })
      .add({
        duration: 500,
        easing: 'easeOutCirc',
        cy: [-100, 0],
      });
    }
  },
  watch: {},
  created: function () {},
  mounted: function () {
    this.initMain();
    this.initIntro();
  }
}
</script>

